.page-header {
	border-bottom: 1px solid #e6e6e6;
}

.page-header__logo[href] {
	transition-property: opacity;
	transition-duration: 0.1s;
	transition-timing-function: linear;

	&:focus {
		opacity: 0.8;
	}

	&:active {
		opacity: 0.6;
	}
}

.page-header__logo-image {
	padding: 0 20px;
	padding-top: 14px;
	padding-bottom: 8px;
}

@media (min-width: $tablet-width) {
	.page-header {
		display: flex;
		align-items: center;
		padding: 0 30px;
		padding-top: 24px;

		border-bottom: none;
	}

	.page-header__logo {
		margin-right: auto;
	}

	.page-header__logo[href] {

		&:hover {
			opacity: 0.8;
		}

		&:active {
			opacity: 0.6;
		}
	}

	.page-header__logo-image {
		padding: 0;
		width: 172px;
		height: 57px;
	}

	.page-header__logo-mobile {
		display: none;
	}

	.page-header__logo-tablet {
		display: block;
	}
}

@media (min-width: $desktop-width) {
	.page-header {
		margin: 0 auto;
		padding: 0 110px;
		padding-top: 54px;
		width: 1220px;
		
		&--background {
			
			&::before {	
				content: "";
				
				position: absolute;
				top: 0;
				right: 0;
				z-index: -1;

				width: 50%;
				height: 696px;
				
				background-color: #65a93c;
				background-image: 
				  url("../img/nutrition-program/index-background-desktop@1x.jpg");
				background-repeat: no-repeat;
				background-position: right 0;
				background-size: 720px;

				@media (min-resolution: $resolution-dpi),
				       (min-resolution: $resolution-dppx) {

					background-image: 
				  	  url("../img/nutrition-program/index-background-desktop@2x.jpg");
				}
			}
		}
	}
	
	.page-header__logo-image {
		width: 202px;
		height: 60px;
	}
}